<template>
  <div>
    <div id="details1">
   </div>
 </div>
</template>

<script>
import Axios from 'axios'
export default{
  methods: {
   Init(url){
      Axios.get(url)
      .then(function(res){
        document.getElementById("details1").innerHTML=res.data.HTML;
      })
      .catch(function(){
        console.log("error");
      })
   }
  }
}
</script>

<style>
#details1{
  width:95%;
  position: relative;
  left:1%;
  background:rgba(234,234,234,0.1);
  border-radius:10px;
  min-height: 400px;
  overflow:visible
}
</style>



