import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

// 应用初始状态
const state ={
  News_All:"news",
  news_key:"",
  ifstate:"",
  card_key:"",
  Second_Acitive:'/',     //用来管理新闻的二级导航栏的正确选择
  InitSecodRouter:[1,2,3],
}

// 定义所需的 mutations
const mutations = {
     
      // alert(state.InitSecodRouter);
    
    showUserName(state){
        alert(state.username);
    },
    DECREMENT(state) {
        state.count--
    },
  /*  setTaskId: (state, name) => {
        state.taskid = name
    }*/
}

// 创建 store 实例
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})



