<template>
  <div>
    <Des ref="Des"></Des>
  </div>
</template>

<script>
import {renderURL} from '../api/httpapi.js'
import Des from '../components/show_html.vue'
export default{
  components:{Des},
  data(){
    return{

    }
  },
  mounted(){
    var key=this.$store.state.card_key;
  //  alert(key);
    if(!key){
                key = JSON.parse(window.localStorage.getItem('carddetail'))
        };
      switch(key){
          case this.$store.state.card_key:
            window.localStorage.setItem('carddetail', JSON.stringify(key));          
            break;         
        };
    var TitleDetailUrl=renderURL+key;
    this.$refs.Des.Init(TitleDetailUrl);
  }
}
</script>

<style>
</style>


